<template>
  <div class="bkw-viewer">
    {{msg}}

    <br>

    <div style="position: relative; width: 100%; height: 100%;">
      <canvas 
        ref="canvas" id="canvas"  
        style="width: 100%; height: 100%;"
        
        @pointerdown="canvas_pointerdown"
        @pointerup="canvas_pointerup"
        @pointermove="canvas_pointermove"
        @contextmenu.prevent 
      />
      <transition appear  name="fade" mode="out-in">
        <div id="lbl-info" 
          v-show="balloonInfo.visible"

          :style="{left:balloonInfo.left, top: balloonInfo.top}"
        >{{balloonInfo.text}}</div>
      </transition>


    </div>

    <br>

    <div>{{ status_text }}</div>
    <div>
        <progress ref="progress" id="progress" value="0" max="100" hidden=1></progress>
    </div>

    <textarea 
      ref="output" id="output" rows="5" readonly style=display:none 
    />


    <br>


  </div>
</template>

<script>
//const test = require('../utils/test')
const loader = require('./load_wasm')
const Elements = require('./Elements')

export default {
  name: 'BkwViewer',
  props: {
    msg: String
  },

  data() {
    return {
      status_text: "Downloading...",

      balloonInfo: {
        visible: false,
        left: '100px',
        top: '100px',
        text: 'Info message',
      },



    }
  },

  methods: {
    myClick(e) {
      console.log('dirname', __dirname)
      //test.test();

      console.log(this.$refs.canvas)

      // this.Module = loader.prepareModule(this.Module, __dirname + 'wasm/');
      //loader.prepareModule(this.Module, __dirname + 'wasm/')
    },

    //------------------------
    canvas_webglcontextlost(e) {
      alert('WebGL context lost. You will need to reload the page.'); 
      e.preventDefault(); 
    },

    //   /. 
    canvas_wheel(e) {
      // console.log('wheel!')
      e.preventDefault(); 
      this.Module.setCameraRot_Fov( -e.deltaY / 50, true, true );
    },

    canvas_pointerdown(e) {
      // console.log('pointerdown')
      e.preventDefault();
      e.target.setPointerCapture(e.pointerId);

      if (this.select_fixtr) {
          this.select_fixtr.diffuse = this.select_fixtr.saved_diffuse; 
          this.select_fixtr.delete(); 
          this.balloonInfo.visible = false; 
      }

      this.select_fixtr = 
        this.Module.getIsectEntity(
          1, // search fixture root node
          e.offsetX, 
          e.offsetY, 
          100000
          //1
        );
      if (this.select_fixtr && !this.select_fixtr.is_node)
        console.log('Geometry ID: ', this.select_fixtr.geometry_id);

      if (this.select_fixtr) {
        this.select_fixtr.saved_diffuse = this.select_fixtr.diffuse;
        this.select_fixtr.diffuse = 0xFFFF0000;
        this.balloonInfo.left = e.offsetX + 'px';
        this.balloonInfo.top  = e.offsetY + 'px';
        this.balloonInfo.text = this.select_fixtr.name;
        this.balloonInfo.visible = true;

        return;
      }

      this.isDownForCamera = true; 
      this.firstDownPos = {
          x: e.offsetX,
          y: e.offsetY
      }

      this.lastDownPos = {
          x: e.offsetX,
          y: e.offsetY
      }


    },

    canvas_pointerup(e) {
      //console.log('pointerup')
      e.preventDefault();
      e.target.releasePointerCapture(e.pointerId);

      if (this.isDownForCamera) {
        this.isDownForCamera = false; 

      }

    },

    canvas_pointermove(e) {
      if (this.isDownForCamera) {
        let dx = e.offsetX - this.lastDownPos.x; 
        let dy = e.offsetY - this.lastDownPos.y; 

        this.lastDownPos = {
            x: e.offsetX,
            y: e.offsetY
        }

        switch (this.dragOperate) {
          case 'rotate':
            this.Module.setCameraRot_X( dy / 5.0 , true, true);
            this.Module.setCameraRot_Y( dx / 5.0 , true, true);
            break; 

          case 'pan':
            //this.Module.setCameraPos(-dx, 0, -dy, true, true);
            break; 
        }
        //console.log(dx, dy); 


      }

    },

    //------------------------

    //    . 
    clearScene() {
      function removeChilds( node ) {
        for (let i=node.childCount-1; i >=0; i--) {
          let child = node.getChild(i); 
          child.removeFromScene();
          child.delete(); 
        }

      }

      removeChilds(this.Module.getRootNode(0));
      removeChilds(this.Module.getRootNode(1));
      removeChilds(this.Module.getRootNode(2));

      this.balloonInfo.visible = false;
      this.select_fixtr = undefined; 

      // TODO:        . 
      this.floor = undefined; 
      this.testFixt_1 = undefined; 
      this.testFixt_2 = undefined; 


    },

    //  
    makeFloor(size_x, size_z) {
      if (this.floor) return; 

      this.floor = new Elements.FloorElement(this.Module, 'floor', size_x, size_z);

      this.Module.setShadowMode(0);
      this.Module.setShadowMode(1);
    },

    //  1 
    makeTextFixt_1(pos_x, pos_z, rot_y) {
      if (!this.floor) return; 
      if (this.testFixt_1) return; 
      this.testFixt_1 = new Elements.TestElement(this.Module, 'test_1', pos_x, pos_z, rot_y); 

      // this.Module.setShadowMode(0);
      // this.Module.setShadowMode(1);
    },

    //  2 
    makeTextFixt_2(pos_x, pos_z, rot_y) {
      if (!this.floor) return; 
      if (this.testFixt_2) return; 
      this.testFixt_2 = new Elements.TestElement(this.Module, 'test_2', pos_x, pos_z, rot_y); 

      // this.Module.setShadowMode(0);
      // this.Module.setShadowMode(1);
    },

    //------------------------

    // .   . 
    printScene() {
      function printStructure( node, prefix = '  ' ) {
          for (let i=0; i<node.childCount; i++) {
              let child = node.getChild(i); 
              console.log(prefix + child.name, child.is_node?'(node)':`gm: ${child.geometry_id}`)
              printStructure( child, prefix + '  ' ); 
              child.delete();
          }
      }

      console.log('\n*base');
      {
        let base = this.Module.getRootNode(0);
        printStructure(base); 
        base.delete();
     }

      console.log('\n*fixt');
      {
        let fixt = this.Module.getRootNode(1);
        printStructure(fixt); 
        fixt.delete();
      }

      console.log('\n*efft');
      {
        let efft = this.Module.getRootNode(2);
        printStructure(efft); 
        efft.delete();
      }




    
    },

  },


  created() {


  },

  mounted() {

    // let bkwChanged = 
    //   (function (bkwName) {
    //     console.log('bkwChanged')
    //   }).bind(this);

    let initApp = 
      (function () {
        //console.log('InitApp')
        //this.Module.setBkwObjectChangedFunc("bkwChanged")
        this.Module.setCameraRotSpeed_Fov(10, 100, 1);

        // Set mouse action
        this.Module.useDefaultMouseAction(false); 

        this.isDownForCamera = false; 
        this.firstDownPos = undefined; 
        this.lastDownPos = undefined; 
        this.select_fixtr = undefined;

        //   
        this.dragOperate = 'rotate';
        //this.dragOperate = 'pan';

        this.Module.setShadowMode(1);


        let canvas = this.$refs.canvas; 

        canvas.addEventListener("webglcontextlost", this.canvas_webglcontextlost, false);

        //   /. 
        canvas.addEventListener("wheel", this.canvas_wheel, {passive: false});


        //   
        // console.log(window);
        window.addEventListener('resize', (function() {
          /*
          let w = canvas.clientWidth;
          let h = canvas.clientHeight;
          if (canvas.width  != w) canvas.width  = w;
          if (canvas.height != h) canvas.height = h;
          this.Module.setRenderSize(w, h);
          */
          console.log('resize')
        }).bind(this));


        //this.Module.setUrlBase('https://oranke.github.io/Misc/ysy_work3/');
        //this.Module.setBkwName('');


      }).bind(this);

    this.Module = {
        preRun: [
          function(){
              // console.log('Prerun 1');
          }
        ],

        postRun: [
          initApp,

          function(){
              // console.log('Postrun 1');
          }
        ],

        canvas: ((function() {
          let canvas = this.$refs.canvas;

          return canvas;
        }).bind(this))(),

        print() {
          let element = this.$refs.output;
          if (element) element.value = ''; // clear browser cache

          return function(text) {
            if (arguments.length > 1) text = Array.prototype.slice.call(arguments).join(' ');
            // These replacements are necessary if you render to raw HTML
            text = text.replace(/&/g, "&amp;");
            text = text.replace(/</g, "&lt;");
            text = text.replace(/>/g, "&gt;");
            text = text.replace('\n', '<br>', 'g');

            console.log(text);

            if (element) {
              element.value += text + "\n";
              element.scrollTop = element.scrollHeight; // focus on bottom
            }
          };
        },

        printErr(text) {
          if (arguments.length > 1) text = Array.prototype.slice.call(arguments).join(' ');
          if (0) { // XXX disabled for safety typeof dump == 'function') {
              dump(text + '\n'); // fast, straight to the real console
          } else {
              console.error(text);
          }
        },


        setStatus : (function(text) {
          if (!this.Module.setStatus.last) 
            this.Module.setStatus.last = { time: Date.now(), text: '' };
          if (text === this.Module.setStatus.text) return;

          let progressElement = this.$refs.progress;

          var m = text.match(/([^(]+)\((\d+(\.\d+)?)\/(\d+)\)/);
          var now = Date.now();
          if (m && now - Date.now() < 30) return; // if this is a progress update, skip it if too soon
          if (m) {
            text = m[1];
            progressElement.value = parseInt(m[2])*100;
            progressElement.max = parseInt(m[4])*100;
            progressElement.hidden = false;
            //spinnerElement.hidden = false;
          } else {
            progressElement.value = null;
            progressElement.max = null;
            progressElement.hidden = true;
            //if (!text) spinnerElement.style.display = 'none';
          }


          //console.log('setStatus', text, '::', this.status_text)
          this.status_text = text;
        }).bind(this),


        test: "1234",
      };


    this.Module = loader.prepareModule(this.Module, __dirname + 'wasm/');
  },


}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#canvas{
  border: 1px black solid; 
}

#lbl-info {
    position: absolute;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */    
    border-radius: 0.5em;

    padding-top: 0.5em;                                  
    padding-bottom: 0.5em;
    padding-left: 1em;
    padding-right: 1em;
    /*
    margin-left: 10px;
    */
    color: white;
    background-color: rgba(0, 0, 0, 0.5);
    text-shadow: 0 0 5px black, 0 0 8px black;
    pointer-events: none;
    font-weight: bold;
    font-size: x-small;

    /*
    left: 10px;
    top: 10px;
    */
    transform: translateY(-50%);

}


h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}

.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

</style>
